/* gabriela-cyrillic-ext-400-normal*/
@font-face {
  font-family: 'Gabriela';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url('./files/gabriela-cyrillic-ext-400-normal.woff2') format('woff2'), url('./files/gabriela-all-400-normal.woff') format('woff');
  unicode-range: U+0460-052F, U+1C80-1C88, U+20B4, U+2DE0-2DFF, U+A640-A69F, U+FE2E-FE2F;
}
/* gabriela-cyrillic-400-normal*/
@font-face {
  font-family: 'Gabriela';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url('./files/gabriela-cyrillic-400-normal.woff2') format('woff2'), url('./files/gabriela-all-400-normal.woff') format('woff');
  unicode-range: U+0400-045F, U+0490-0491, U+04B0-04B1, U+2116;
}
/* gabriela-latin-400-normal*/
@font-face {
  font-family: 'Gabriela';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url('./files/gabriela-latin-400-normal.woff2') format('woff2'), url('./files/gabriela-all-400-normal.woff') format('woff');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}
